import React, { useContext, useEffect, useRef, useState } from 'react'
import { Link, useLocation } from 'react-router-dom'
import Banner from '../../Website/Body/Banner'
import Articles from '../../Website/Body/Home/Articles'
import './Articlepage.css'
import { useReadingTime } from "react-hook-reading-time";
import { ContextApp } from '../../../ContextAPI'
import { convertDate, determineLink } from '../../../Functions'
import Articlesection from './Articlesection'
import Imgloaded from '../Imgloaded/Imgloaded'

const Articlepage = (props) => {
  const {setPercent, setShowBar, artists, articles, ourteam} = useContext(ContextApp)
  const {article, i, id} = props
  const articleref = useRef()
  const author = artists.find(({acf})=> acf.id === article.id)
  const [hash, setHash] = useState('')

  const readtime = useReadingTime(Object.keys(article.content).map(key=> article.content[key].text).join(','))
  const location  = useLocation()

  
  const articlecontrow =  Object.keys(article.content).map((key)=> {
    const root = article.content[key]
    return <Articlesection setHash={setHash} root={root} keyC={key}/>
  })
  const achorlinksrow =  Object.keys(article.content).map((key)=> {
    const root = article.content[key]
    if(root.title) {
      return (
        <a 
         onClick={()=> setHash(key)}
         className={`anchorlink ${hash === key?'activeanchor':''}`} 
         href={`#${key}`} >
          {root.anchor}
        </a>
      )
    }
  })
  const categoriesrow = article.category.map(el=> {
    return (
      <div className="category">
        {el}
      </div>
    )
  })
  const handleScroll = () => {

    const scrolled = document.documentElement.scrollTop
    const height = articleref.current?.clientHeight
    setPercent((scrolled/height)*100)
   
  }
  useEffect(()=>{
    window.addEventListener('scroll', handleScroll)
    setShowBar(true)
    return ()=> {
      setShowBar(false)
      window.removeEventListener('scroll', handleScroll)
    }
  },[])
  // useEffect(()=> {
  //   window.addEventListener("hashchange", function () {
  //     window.scrollTo(window.scrollX, window.scrollY + 80);
  //  })
  // },[])

  return (
    <div className="articlepage">
        <Banner className='herobanner articlebanner'>
          <Imgloaded img={article.img} alt=""/>
          <div className="articleinfobanner">
              <small className="info">
                <span>{article.category[0]}</span>
                <span>{convertDate(new Date(Date.parse(article.date)))}</span>
                <span>{readtime.text}</span>
              </small>
              <h2>{article.title}</h2>
               <div className="by">
                <Imgloaded img={author?.acf.img} alt=""/>
                <span>
                  <Link to={determineLink(artists, author)}>
                    {author?.acf.name}
                  </Link>
                </span>
              </div>
            </div>
               
        </Banner>
        <div className="articleinfo">
            <div className="articlecontpage" ref={articleref}>
                <div className="anchorlinkscont">
                  <div className="anchorlinks">
                    {achorlinksrow}
                  </div>
                </div>
                <div className="inngercon">
                  {articlecontrow}
                </div>
            </div>
            <div className="categories">
              {categoriesrow}
            </div>
        </div>
        <Articles 
        title={`View Similar Articles By ${author?.acf.name}`}
        text='Read similar articles'
        articles={articles.filter((arti)=> arti.acf.id === article.id && arti.id !==id)}/>
    </div>
  )
}
export default Articlepage