import React from 'react'
import { Link } from 'react-router-dom'
import Imgloaded from '../Imgloaded/Imgloaded'
import './Exhibitioncard.css'

const Exhibitioncard = (props) => {
  const {i} = props
  const {img, text, title, link} = props.exhibition


  return (
    <div className={`exhibtitioncard ${i%2?'rowreverse':''}`} data-aos={i%2?'fade-right':'fade-left'}>
        <div className="exhibitionimg imgcontainer">
          <Imgloaded img={img} />
        </div>
        <div className="exhibtioncont">
          <h2>
            <Link className='exhibitionlink' to={link}>
              {title}
            </Link>
          </h2>
          <p>{text}</p>
        </div>
    </div>
  )
}
export default Exhibitioncard