import React from 'react'
import './Iconbox.css'

const Iconboxcont = (props) => {
  const {text, icon, title, aos, href} = props
  
  return (
    <div className="iconcont" data-aos={aos}>
      <div className="iconcon">
        <i className={icon}></i>
      </div>
      <div className="contentbox">
        <h3>{title}</h3>
        {
        href?
        <a href={href} target='__blank'>{text}</a>
        :
        <p>{text}</p>
        }
      </div>
    </div>
  )

}
export default Iconboxcont