import React, { useEffect } from 'react'
import './Animateddownarrow.css'

const Animateddownarrow = (props) => {

  const {href} = props

  return (
      <a href={`#${href}`} className="animateddownarrow">
        <div className="animateddownarrowdiv">
        <span className="themeicon"></span>
          <div className="plate"></div>
        </div>        
      </a>
  )
}
export default Animateddownarrow