import React, { useContext, useEffect, useState } from 'react'
import { useLocation } from 'react-router-dom'
import { navLinks } from '../../../Arrays'
import { ContextApp } from '../../../ContextAPI'
import Logo from '../../Reusable/Logo/Logo'
import Menu from './Menu'
import './Navbar.css'
import Navbarlink from './Navbarlink/Navbarlink'
import Navmenuaccordion from './Navmenuaccordion'

const Navbar = () => {
  const location  = useLocation()
  const [navMenu, setNavMenu] = useState(false)
  const {scrolled, setScrolled, showBar, percent}= useContext(ContextApp)
  const navLinksrow = navLinks.map(link=> {
    return <Navbarlink link={link}/>
  })
  function handleScroll(){
    // if(window.scrollY > 100 && (location.pathname ==='/')) {
    //   setScrolled(true)
    // }
    // else {
    //   const path = location.pathname
    //   if(
    //    path !== '/'
    //   ){
    //     setScrolled(true)
    //   }
    //   else {
    //     setScrolled(false)
    //   }
    // }
   
      if(window.scrollY > 100) {
        setScrolled(true)
      }
      else {
        setScrolled(false)
      }
    
  }
  useEffect(()=>{
    // if(location.pathname === '/') {
    // window.addEventListener('scroll', handleScroll)
    // }
    // return  () => {
    //   setScrolled(true)
    //   window.removeEventListener('scroll', handleScroll)
    // }
    window.addEventListener('scroll', handleScroll)

  },[location])
  return (
  <>
    <div className={(scrolled)?"navbar scrollednav":'navbar'}>
      <Logo img={scrolled?'https://0xsociety.com/wp-content/uploads/2021/06/Q9So4Gu.png':'https://0xsociety.com/wp-content/uploads/2021/06/ZmFjLfm.png'}/>
      <div className="links">
        {navLinksrow}
      </div>
     {showBar&&<div style={{width: percent+'%'}} className="progressbar"></div>}
      <Menu  navMenu={navMenu} setNavMenu={setNavMenu}/>
    </div>
    <div className={navMenu?'navmenu navmenu-enter':'navmenu navmenu-exit'}>
        <Logo img='https://0xsociety.com/wp-content/uploads/2021/06/Q9So4Gu.png'/>
        <div className="linksmenu">
        {navLinks?.map(link=>{
          return <Navmenuaccordion setMenu={setNavMenu} column={link}/>
        })}
        </div>
      </div>
    </>
  )
}
export default Navbar