import React from 'react'
import { columns, socialicons } from '../../../Arrays'
import Logo from '../../Reusable/Logo/Logo'
import Column from './Column/Column'
import './Footer.css'

const Footer = (props) => {

  const columnsrow = columns?.map(column=>{
    return (
      <Column notitlelink column={column} />
    )
  })
  const socialiconsrow = socialicons?.map(icon=>{
    return (
      <a href={icon.link} target='__blank'>
        <i className={icon.icon+' socialicon'}></i>
     </a>
    )
  })
  const date = new Date()
  return (
    <div className="footer">
     <div className="columns">
        <Logo img='https://0xsociety.com/wp-content/uploads/2021/06/Q9So4Gu.png'/>
        {columnsrow}
     </div>
     <div className="rights">
        <h5 className="graytext">
          <span>
          All Rights Reserved 0xSociety {date.getFullYear()}, made by NeoLabs
          </span>
        </h5>
     </div>
     <div className="socialicons">
          {socialiconsrow}
        </div>
    </div>
  )
}

export default Footer 