
export function convertDate(date, noday) {
  if(noday) {
    return (
      date.toLocaleString('en-US', {  year: 'numeric', month: 'long', day: 'numeric' })
    )
  }
  else {
    return (
      date.toLocaleString('en-US', { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' })
    )
  }
}
export function determineLink(artists, author) {
  if(typeof artists.find(x=> x.acf.id === author.acf.id) !=='undefined'){
    return `/our-artists/${author?.acf.id}`
  }
  else {
    return `/our-team/${author?.acf.id}`
  }
}
export   var logger = function()
{
    var oldConsoleLog = null;
    var pub = {};

    pub.enableLogger =  function enableLogger() 
                        {
                            if(oldConsoleLog == null)
                                return;

                            window['console']['warn'] = oldConsoleLog;
                        };

    pub.disableLogger = function disableLogger()
                        {
                            oldConsoleLog = console.warn;
                            window['console']['warn'] = function() {};
                        };

    return pub;
}();