import React from 'react'
import Exhibitioncard from '../Exhibitioncard'
import Exhibitionskeleton from '../Exhibitionskeleton'
import Card from './Card'
import './Exhibitionpage.css'

const Exhibitionpage = (props) => {

  const {exhibitions, el, exhibitiontypes} = props

  const exhibitiontypesrow = exhibitiontypes.filter(x=> x.type !== el.type).map((exhibition, i)=> {
    return (
      <Exhibitioncard exhibition={exhibition} i={i}/>
    )
  })
  const exhibitionsrow = exhibitions?.map(({acf})=> {
    return (
     <Card el={acf}/>
    )
  })

  return (
    <div className="exhibitionpage">
        <div className="exhibitions">
          <h2  className='ttt'>All {el.title}</h2>
          <div className="innerexhibitions">
            {
              exhibitionsrow.length !==0?
              exhibitionsrow
              :
              <>
                <Exhibitionskeleton />
                <Exhibitionskeleton />
                <Exhibitionskeleton />
              </>
            }
          </div>
        </div>
        <div className="exhibitions ">
         <h2 className='ttt'>View Other Exhibitions</h2>
        <div className="innerexhibition">
          {exhibitiontypesrow}
        </div>
      </div>
    </div>
  )
}
export default Exhibitionpage