import React from 'react'
import { Link, Route } from 'react-router-dom'
import Appbtn from '../../../../../../Reusable/Appbtn/Appbtn'
import Imgloaded from '../../../../../../Reusable/Imgloaded/Imgloaded'
import Titleborder from '../../../../../../Reusable/Titleborder'
import './Artistcard.css'

const Artistcard = (props) => {
 
  const {artist, artists, id, i} = props
  return (
  <>
    <div className="artistcard" data-aos={(i%2)?'fade-right':'fade-left'}>
      <div className="leftcard">
        <Imgloaded img={artist.img} alt=""/>
      </div>
      <div className="rightcard">
        <h2>
          <span>{artist.name}</span>
          <small className="artisttype">
            {artist.type}
          </small>
        </h2>
        <span className="graytext">
          <i className='fa fa-quote-left'></i>
          <small>{artist.quotes.split('+')[0]}</small>
        </span>
        <Link  to={'/our-artists/'+artist.id}>
          <Appbtn className='reverse' icon='fa fa-long-arrow-right' text='Read More'/>
        </Link>
      </div>
    </div>
  </>
  )
}
export default Artistcard