import React, { useContext } from 'react'
import { ContextApp } from '../../../../../../ContextAPI'
import Imgloaded from '../../../../../Reusable/Imgloaded/Imgloaded'
import Articles from '../../../Home/Articles'
import './Memberpage.css'

const Memberpage = (props) => {
  const {articles} = useContext(ContextApp)
  const {person} = props
  const works = articles.filter(x=> x.acf.id === person.id)

  return (
    <div className="memberpage">
      <div className="teammemberbanner">
          <div className="teammemberimg">
            <Imgloaded img={person?.image} alt=""/>
          </div> 
          <div className="teammembercont">
            <h2>{person?.name}</h2>
            <small>{person?.type}</small>
            <p>{person?.description}</p>
          </div>
          <div className="contacticons">
      {person.email && <a href={`mailto:${person?.email}`}>
        <i className='fa fa-envelope'></i>
       </a>}
      {person.linkedin && <a target='__blank' href={person?.linkedin}>
        <i className='fab fa-linkedin-in'></i>
       </a>}
      </div>
      </div>
      <Articles
      sublink={'artworks'}
      text={'Discover Articles Written by '+person.name}
      title='Articles'
      articles={works}
      />

    </div>
  )
}
export default Memberpage