import React, { useContext, useEffect } from 'react'
import { Route, Switch, useLocation } from 'react-router-dom'
import { accessinfo, exhibitiontypes, navLinks, partners } from '../../../Arrays'
import { ContextApp } from '../../../ContextAPI'
import Articlepage from '../../Reusable/Article/Articlepage'
import Exhibitioncard from '../../Reusable/Exhibitioncard/Exhibitioncard'
import Exhibitionpage from '../../Reusable/Exhibitioncard/Exhibitionpage/Exhibitionpage'
import Iconbox from '../../Reusable/Iconbox/Iconbox'
import Templatebanner from '../../Reusable/Templatebanner/Templatebanner'
import TemplateBannerPage from '../../Reusable/Templatepage/TemplateBannerPage'
import Typeonload from '../../Reusable/Typeonload/Typeonload'
import Footer from '../Footer/Footer'
import Banner from './Banner'
import './Body.css'
import Home from './Home/Home'
import About from './Pages/About/About'
import Contact from './Pages/Contact/Contact'
import Events from './Pages/Events/Events'
import Learn from './Pages/Learn/Learn'
import LostPage from './Pages/LostPage/LostPage'
import Member from './Pages/Member/Sendemail'
import Ourstory from './Pages/Ourstory/Ourstory'
import Artistpage from './Pages/Programs/Artists/Artistcard/Artistpage'
import Artists from './Pages/Programs/Artists/Artists'
import Artwork from './Pages/Programs/Artwork/Artwork'
import Programs from './Pages/Programs/Programs'
import AOS from 'aos'
import Blog from './Pages/Blog/Blog'
import Article from '../../Reusable/Article/Article'
import Appbtn from '../../Reusable/Appbtn/Appbtn'
import Memberpage from '../Body/Pages/Ourstory/Teammemberpage/Memberpage'
import Logo from '../../Reusable/Logo/Logo'
import Grant from './Pages/Grant/Grant'
import Imgloaded from '../../Reusable/Imgloaded/Imgloaded'
const Body = () => {

  const {
    scrolled, 
    artists, 
    artworks, 
    articles, 
    exhibitions, 
    events,
    ourteam
  } = useContext(ContextApp)
  const now = Date.now()
  const routes = navLinks.map(route=> {
   const Components = {
    About,
    Learn, 
    Programs,
   }
    const El = Components[route.component]
   
    return (
      <Route path={`/${route.link}`}>
          <El />
      </Route>
    )
  })

  const artistsroutes = artists?.map((artist)=> {
    return (
      <Route  path={'/our-artists/'+artist.acf.id}>
        <Artistpage 
          artist={artist.acf} 
          works={artworks.filter(x=> x.acf.id === artist.acf.id)}/>
      </Route>
    )
  })
  const teammemberroutes = ourteam?.map((person)=> {
    return (
      <Route  path={'/our-team/'+person?.acf.id}>
        <Memberpage person={person?.acf}/>
      </Route>
    )
  })
  const articleroutes = articles?.map((article)=> {
    return  (
      <Route exact path={`/articles/${article.id}`}>
        <Articlepage article={article.acf} id={article.id}/>
      </Route>
    )
  })
  const artworksrow = artworks.map((artwork)=> {
    return ( 
      <Route path={`/artworks/${artwork.id}`}>
        <Artwork artwork={artwork.acf} id={artwork.id}/>
      </Route>
    )
  })

  const partnersrow = partners.map(partner=> {
    return  (
      <div className="partner" data-aos={partner.aos}>
        <div className="contpartner">
          <h2>{partner.partner}</h2>
          <div className="partnerparas">
            {partner.paragraphs.map(x=> {
              return (
                <p>{x}</p>
              )
            })}
          </div>
          <a href={partner.link} target='__blank'>
          <span className="themeicon mr"></span>
            <span>Learn More</span>
          </a>
        </div>
        <Imgloaded className='immg' img={partner.img} alt=""/>

      </div>
    )
  })


  const exhibitiontypesrow = exhibitiontypes.map((exhibition, i)=> {
    return (
      <Exhibitioncard exhibition={exhibition} i={i}/>
    )
  })

   const exhibitionpages = exhibitiontypes.map(el=> {

    return (
      <TemplateBannerPage 
        title={el.title} 
        text={`View 0x ${el.title}`}
        classNameTitle='logofont'
        path={'/'+el.link}
        img={el.img}
      >
        <Exhibitionpage 
          exhibitiontypes={exhibitiontypes} 
          exhibitions={exhibitions[el.type]} el={el}/>
      </TemplateBannerPage>
    )
  })

  const location = useLocation()
  useEffect(()=>{
    AOS.init()
  },[location])

  return (
    <div className="body">
        <Switch>
            <Route exact path='/'>
              <Home />
            </Route>
            {artistsroutes}
            {teammemberroutes}
          <Route  path='/our-artists'>
            <Artists />
          </Route>
          <Route path='/our-story'>
            <Ourstory />
          </Route>
          <TemplateBannerPage
          className='academypage'
          img='https://0xsociety.com/wp-content/uploads/2021/06/gradient-0xAcademy-3-scaled.jpg'
          path='/academy'
          title={<><Logo nolink  img='https://0xsociety.com/wp-content/uploads/2021/06/logo-5.png'/></>}
          text='0x Society presents 0x Academy'
          classNameTitle='logofont'
          >
          <div className="academyinfo">
              <div className='inneracademyinfo'>
                  <h2>0x Society has embarked on a mission to introduce more artists and collectors to the world of crypto art by demystifying its intricacies and shining a light on its potential.</h2>
                  <div className="inneracadmysteps">
                    <p className='step' data-aos='fade-right'><span className='stepnum'><i className='fa fa-school'></i></span> 0x Academy, presented by 0x Society, is the education and onboarding platform for artists and collectors interested in crypto art. 0x Academy offers thorough guides, tips, and tricks to help you get involved in the industry faster.</p>
                    <p className='step' data-aos='fade-left'><span className='stepnum'><i className='fa fa-school'></i></span> Learn from the experience of some of the top crypto art collectors and artists and make the jump today!</p>
                  </div>
                  <strong>0x Academy is the Crypto Art Knowledge Base.</strong>
                  <a href="https://academy.0xsociety.com/" target='__blank'>
                    {/* <img className='acadmybtn' src="https://0xsociety.com/wp-content/uploads/2021/06/wbGgiEL.png" alt=""/> */}
                    <Appbtn text='Learn More' icon='fa fa-long-arrow-right'/>
                  </a>
              </div>
            </div>
          </TemplateBannerPage>
          <TemplateBannerPage 
            title='Our Partners' 
            text='Learn about our partnership with SuperRare and Foundation'
            classNameTitle='logofont'
            path='/our-partners'
            >
             <div className="partners">
                <div className="innerpartners inneracademyinfo">
                  {partnersrow}
                </div>
              </div>
          </TemplateBannerPage>
          <TemplateBannerPage 
            title={<Logo img='https://0xsociety.com/wp-content/uploads/2021/06/Grant.png' nolink/>}
            text='Learn about our grant program'
            classNameTitle='logofont'
            path='/grant-program'
            className='noborder' 

          >
            <Grant />
          </TemplateBannerPage>
          <TemplateBannerPage
            className='noborder' 
            title={<Logo img='https://0xsociety.com/wp-content/uploads/2021/06/exhibitions-01.png' nolink/>}
            text='Discover past, current and upcoming exhibitons.'
            path='/exhibitions'
            img={'https://0xsociety.com/wp-content/uploads/2021/06/gradient-0xSociety-scaled.jpg'}
          >
          <div className="exhibitions">
              <div className="innerexhibition">
                {exhibitiontypesrow}
              </div>
            </div>
          </TemplateBannerPage>
          {/* <TemplateBannerPage
           title='Blog' 
           text='0x Blog'
           path='/blog'
          >
            <Blog />
          </TemplateBannerPage> */}
          <TemplateBannerPage 
            path='/blog'
            exact
            className='noborder' 
            title={<Logo img='https://0xsociety.com/wp-content/uploads/2021/06/blog-01.png' nolink/>}
            text='Discover Blog'
            img={articles[0]?.acf.img}
           >
            <Events title='Recent News and Blog' row={articles} component='Article'/>
          </TemplateBannerPage>
          <TemplateBannerPage 
            path='/artworks'
            exact
            className='noborder' 
            title={'Artworks'}
            text='Discover Artworks'
            img={artworks[artworks.length - 3]?.acf.img}
           >
            <Events hidefilters link='artworks' title='Artworks' row={artworks} component='Article'/>
          </TemplateBannerPage>
          <TemplateBannerPage 
            path='/events'
            className='noborder' 
            title={<Logo img='https://0xsociety.com/wp-content/uploads/2021/06/events-01.png' nolink/>}
            text='Learn more about 0x Society Events'
            img={events[0]?.acf.img}
           >
            <Events 
              title='Upcoming Events' 
              row={events.filter(x=> Date.parse(x.acf.date) >= now)}
              component='Eventcard'/>
                <Events 
              title='Past Events' 
              row={events.filter(x=> Date.parse(x.acf.date) < now)}
              component='Eventcard'/>
          </TemplateBannerPage>

          {articleroutes}
          {routes}
          {artworksrow}
          {exhibitionpages}
          {/* <Route path='/become-member'>
            <Member />
          </Route> */}
          {/* <Route path='/contact'>
            <Contact />
          </Route> */}
          <Route>
              <LostPage />
          </Route>
        </Switch>
      <span style={{cursor: 'pointer'}} className={`scrolltotop ${scrolled&&'activescroll'}`} 
      onClick={()=> window.scrollTo({
        top: 0,
        behavior: 'smooth'
      })}></span>
      <Footer />
    </div>
  )
}
export default Body