import React, { useEffect, useState } from 'react'
import { Route } from 'react-router-dom'
import Imgloaded from '../Imgloaded/Imgloaded'
import Titleborder from '../Titleborder'
import './Templatepage.css'

const TemplateBannerPage = (props) => {

  const {title, text, img='https://0xsociety.com/wp-content/uploads/2021/06/gradient-0xSociety-scaled.jpg', path, className} = props

  return (
    <Route path={path}>
    <div className={"templatebannerpage "+ className}>
      <div className="thb">
        <Titleborder title={title} />
        <Imgloaded img={img}/>
      </div>
       {props.children}
    </div>
    </Route>
  )
}
export default TemplateBannerPage