import React from 'react'
import { Link } from 'react-router-dom'
import Appbtn from '../../../../../Reusable/Appbtn/Appbtn'
import Imgloaded from '../../../../../Reusable/Imgloaded/Imgloaded'
import './Program.css'

const Program = (props) => {

  const {program, programs} = props
  
  const LinkTag = program.href?'a':Link

  return (
    <div className="program" data-aos={programs.indexOf(program)%2?'fade-right':'fade-left'}>
      <div className="programimg">
         <Imgloaded img={program.img}/>
      </div>
      <div className="programcont">
        <h2>{program.title}</h2>
        <p className="graytext">{program.text}</p>
        <p>{program.description}</p>
       <LinkTag to={`/${program.link}`} href={program.href} target={program.href?'__blank':''}>
        <Appbtn text='Learn More' icon='fa fa-long-arrow-right' className='reverse'/>
       </LinkTag>
      </div>
    </div>
  )
}
export default Program