import React from 'react'
import Titleborder from '../Titleborder'
import './Containerrow.css'

const Containerrow = (props) => {

  const {row, title, className, titleborder} = props

  return (
    <div className={"containerrow "+className}>
      {titleborder?<Titleborder title={title}/>:<h2>{title}</h2>}
      <div className="innercontainerrow">
        {row}
      </div>
    </div>
  )
}
export default Containerrow