import React, { useContext } from 'react'
import { Link } from 'react-router-dom'
import { ContextApp } from '../../../ContextAPI'
import Appbtn from '../Appbtn/Appbtn'
import Imgloaded from '../Imgloaded/Imgloaded'
import Titleborder from '../Titleborder'

const Slide = (props) => {
  const {name, description, img, color, aos, id} = props.slide
  const {artworks} = useContext(ContextApp)
 
  const artwork = artworks.find(x=> x.acf.id === id)?.acf

  const artworkimg = artwork?.img
  return (
      <div className="slidec">
        <div>
          <p>
          <h1>{name}</h1>

            <span>
            {description.length>400?description.slice(0, 400)+'...':description}
            </span>
           <Link to={`/our-artists/${id}`}>
            <Appbtn icon='fa fa-chevron-right' text='Learn More'/>
           </Link>
          </p>
          <div className="img">
            <Imgloaded img={img} alt=""/>
            <div className="circle">
              {artworkimg?<Imgloaded img={artworkimg} alt=""/>:''}
            </div>
          </div>
        </div>
      </div>
  )
}
export default Slide