import React, { useState, useEffect, useContext } from 'react'
import { artists } from '../../../Arrays'
import { ContextApp } from '../../../ContextAPI'
import Carouselcont from '../Carousel/Carousel'
import Slide from './Slide'
import './Slideshow.css'
const SlideShow = (props) => {

  const {artists} = useContext(ContextApp)
  const [slide, setSlide] = useState(0)

  
  useEffect(()=>{
    setSlide(0)
  },[])

  useEffect(()=> {
    let intervalId = setInterval(()=> {
      setSlide(prev=> prev + 1)
    }, 1000) 
    return  () => {
      clearInterval(intervalId)
    }
  }, [])

  return (
    
    <div className="nextprevtab">
      {/* <div className="slides">
        { artists?.map(({acf}, i)=>{
           return (
             <div className={`slidetab ${i === slide?'activeslidetab':''} ${i-1 >= slide?'righttab':i+1<=slide?'lefttab':''}`}>
               <Slide slide={acf}/>
             </div>
           )
         })}
      </div>
      <div className="slidesbtn">
        <strong className='bubbles'>Artists</strong>
          <div>
            {artists?.map((content, i)=>{
            return (
              <span className={slide=== i?'active':''} onClick={()=> setSlide(i)}>0{i+1}</span>
            )
          })}
          </div>
      </div> */}
      <Carouselcont artists={artists} autoplay itemsrow={ artists?.map(({acf}, i)=>{
           return (
             <div className={`slidetab`}>
               <Slide slide={acf}/>
             </div>
           )
         })}/>
    </div>
  )
}
export default SlideShow
