import React from 'react'

const Quote = (props) => {

  const {quote, skeleton} = props

  return  (
    <div className="quote">
        <i className='fa fa-quote-left'></i>
        {skeleton?<span className="skeletonquote"></span>:<span className="graytext">{quote}</span>}
    </div>
  )
}
export default Quote