import React from 'react'
import Iconbox from './Iconbox'
import './Iconbox.css'
const Skill = (props) => {

  const {skill} = props

  return (
    <div className="skillbox">
 
      <Iconbox icon={'fa fa-paint-brush'}/>
      <div>
        <span>{skill}</span>
      <div className="percent">
          <div className="percentage" style={{width: 100+'%'}}></div>
       
       </div>
      </div>
    
    </div>
  )
}
export default Skill