import React, { createContext, useEffect, useState } from 'react'
import axios from 'axios';
import { logger } from './Functions';


export const ContextApp = createContext()

const ContextAppProvider = props => {
  const clean = text => text?.replace(/[^a-zA-Z0-9 ]/g, "");
  const [scrolled, setScrolled] = useState(false)
  const [showBar, setShowBar] = useState(false)
  const [percent, setPercent] = useState(0)

  const [artists, setArtists] = useState([])
  const [ourteam, setOurteam] = useState([])
  const [exhibitions, setExhibitions] = useState({
    past: [],
    current: [],
    upcoming: []
  })
  const [events, setEvents] = useState([])
  const [artworks, setArtworks] = useState([])
  const [articles, setArticles] = useState([])


  useEffect(()=>{
    
    //Articles
    axios.get('https://0xsociety.com/wp-json/wp/v2/articles')
    .then(res=> {
      setArticles(res.data)
    })
    .catch(err=> console.log(err))
    
    //Artists
    axios.get('https://0xsociety.com/wp-json/wp/v2/artist')
    .then(res=> {
      setArtists(res.data)
    })
    .catch(err=> console.log(err))
    
    //Our Team
    axios.get('https://0xsociety.com/wp-json/wp/v2/ourteam')
    .then(res=> {
      setOurteam(res.data.sort((a, b) => a.acf.type.localeCompare(b.acf.type)))
    })
    .catch(err=> console.log(err))
    
    //Exhibitions
    axios.get('https://0xsociety.com/wp-json/wp/v2/exhibitions')
    .then(res=> {
      const dateFilteredExhibitions = res.data.reduce((result, exb)=> {
        const now = Date.now()
        if(now < +new Date(Date.parse(exb.acf.startdate))) {
          result.upcoming.push(exb)
        }
        else if (now > +new Date(Date.parse(exb.acf.enddate))){
          result.past.push(exb)
        }
        else {
          result.current.push(exb)
        }
        return result
      }, {past: [], current: [], upcoming: []})
      setExhibitions(dateFilteredExhibitions)
    })
    .catch(err=> console.log(err))

    //Events
    axios.get('https://0xsociety.com/wp-json/wp/v2/events')
    .then(res=> {
      setEvents(res.data)
    })
    .catch(err=> console.log(err))

    //Artworks
    axios.get('https://0xsociety.com/wp-json/wp/v2/artworks')
    .then(res=> {
      setArtworks(res.data)
    })
    .catch(err=> console.log(err))
  },[])
  useEffect(()=> {
    console.log('something')
  }, [])

  useEffect(()=>{
     logger.disableLogger()
  },[])  

return <ContextApp.Provider 
      value={{ 
        scrolled, setScrolled,
        showBar, setShowBar,
        percent, setPercent,
        clean,
        artists,
        ourteam,
        exhibitions,
        events,
        artworks,
        articles,

      }}>
    {props.children}
</ContextApp.Provider>
}
export default ContextAppProvider