import "./styles.css";
import React, { useEffect } from 'react'
import { BrowserRouter as Router, Redirect, Route, Switch, useLocation } from 'react-router-dom'
import ContextAppProvider from "./ContextAPI";
import ScrollToTop from "./Components/Reusable/ScrollToTop";
import Body from "./Components/Website/Body/Body";
import AOS from 'aos'
import Navbar from './Components/Website/Navbar/Navbar'
import Footer from "./Components/Website/Footer/Footer";
import Newsletter from "./Components/Website/Body/Pages/Member/Newsletters";
import Iconbox from "./Components/Reusable/Iconbox/Iconbox";


export default function App() {
  const handleScroll = () => {
    AOS.refresh()
  }
  useEffect(()=>{
    window.addEventListener('scroll', handleScroll)
  },[])
  useEffect(()=>{
    AOS.init()

  },[])
 
  useEffect(()=> {
    document.querySelectorAll('a[href^="#"]').forEach(anchor => {
      anchor.addEventListener('click', function (e) {
          e.preventDefault();
  
          document.querySelector(this.getAttribute('href')).scrollIntoView({
              behavior: 'smooth'
          });
      });
  });

  }, [])

  return (
    <Router>
    <ScrollToTop />

     <ContextAppProvider >

        <Navbar />
        <Body />
    </ContextAppProvider>
  </Router>
  );
}
