import React from 'react'
import { Link } from 'react-router-dom'
import { programs } from '../../../../../Arrays'
import Templatebanner from '../../../../Reusable/Templatebanner/Templatebanner'
import TemplateBannerPage from '../../../../Reusable/Templatepage/TemplateBannerPage'
import Container from './Container'
import './Learn.css'

const Learn = () => {
  const society = programs.find(x=> x.academy)
  const grantprogram = {
      title: '0x Grant Program',
      text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irur',
      img: 'https://0xsociety.com/wp-content/uploads/2021/06/6TrEDt4.jpg',
      icon: 'fa fa-hand-holding-usd',
      link: 'grant-program'
    }
  const blog = {
    title: '0x Blog',
    text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irur',
    img: 'https://0xsociety.com/wp-content/uploads/2021/06/sLSSe2c-1-scaled.jpg',
    icon: 'fa fa-blog',
    link: 'blog'
  }

  return (
    <div className="learn">

      <TemplateBannerPage 
        path='/learn' 
        img={society.img}
        title='Learn More About 0x Society'
        text='Discover 0x Academy and 0x Grant Program' 
        >
      <div className="learncont">
          <h2>0x Grant Program and 0x Academy</h2>

          <div className="innerlearncont">
          <Container cont={society} href={true}/>
          <Container cont={grantprogram} />
          <Container cont={blog} />
          </div>
        </div>
      </TemplateBannerPage>
    </div>
  )
}
export default Learn