import React, { useEffect, useRef } from 'react'
import Imgloaded from '../Imgloaded/Imgloaded'


const Articlesection = (props) => {
  const {root, keyC, setHash} = props
  const scrollref = useRef()
  const determinRender = ()  =>  {
    if(root.img) {
      return (
        <div  className="paragraph" id={keyC}>
          {root.title&&
          <h2>{root.title}</h2>
          }
          <p>{root.text}</p>
          <div className="paragraphimg">
            <Imgloaded img={root.img} alt=""/>
            <small >{root.caption}</small>
          </div>
        </div>
      )
    }
    else if (root.title) {
      return (
        <div className="paragraph" id={keyC}>
          <h2>{root.title}</h2>
          <p>{root.text}</p>
        </div>
      )
    }
    else {
      return (
        <div className="paragraph" id={keyC}>
          <p>{root.text}</p>
        </div>
      )
    }
  
  }
  const handleScroll = () => {
      if(typeof scrollref.current !=='undefined') {
        const rect = scrollref.current.getBoundingClientRect()

        if(rect.top >=0 && rect.y <=100) {
          setHash(keyC)
        }
      }  
    }
  useEffect(()=>{
    window.addEventListener('scroll', handleScroll)
  
    return ()=> {
      window.removeEventListener('scroll', handleScroll)
    }
  },[])

  return (
    <div className='trackscroll' ref={scrollref}>
    {determinRender()}
    </div>
  )
}
export default Articlesection