export const questionslides  = [
  {
    type: 'logo',
    strong: '0x Society has embarked on a mission to introduce more artists and collectors to the world of crypto art by demystifying its intricacies and shining a light on its potential.',
    content: <div className='slideparagraphs'>
      <strong>0x Society has embarked on a mission to introduce more artists and collectors to the world of crypto art by demystifying its intricacies and shining a light on its potential.</strong>
      <p>0x Academy, presented by 0x Society, is the education and onboarding platform for artists and collectors interested in crypto art. 0x Academy offers thorough guides, tips, and tricks to help you get involved in the industry faster.</p>
      <p>Learn from the experience of some of the top crypto art collectors and artists and make the jump today!</p>
    </div>,
    link: 'https://app.gitbook.com/@0x-society/s/0x-academy/'
  },
  {
    type: 'question', 
    question: 'What is digital art?',
    keywords: 'digital art',
    content: <div className='slideparagraphs'>
      <p>
        <span>Digital art is defined as art that <strong>uses digital technology in any of three ways: as the product, as the process, or as the subject.</strong> Ultimately, digital art is art that could not otherwise exist without digital technology.</span>
      </p>
    </div>,
    link: 'https://app.gitbook.com/@0x-society/s/0x-academy/guides/what-is-digital-art '
  },
  {
    question: 'What are NFTs',
    keywords: 'NFTs',
    type: 'question',
    word: 'Non-fungible token | noun',
    definition: 'a unique digital identifier that cannot be copied, substituted, or subdivided, that is recorded in a Blockchain, and that is used to certify authenticity and ownership (as of a specific digital asset and specific rights relating to it)',
    definitionlink: 'https://www.merriam-webster.com/dictionary/non-fungible%20token',
    definitionlinktext: 'Merriam-Webster dictionary',
    content: <div className='slideparagraphs'>
      <p>In economics, fungibility is the property of a good or a commodity whose individual units are essentially interchangeable, and each of its parts is indistinguishable from another part. </p>
      <p>In the digital world, NFTs are "one-of-a-kind" assets that can be bought and sold like any other piece of property - but they are not tangible assets. These digital tokens can be thought of as certificates of ownership for virtual or physical assets, like a deed to a house. However, these digital certificates of ownership are far more secure than the original certificates of ownership we know. </p>
    </div>,
    link: 'https://app.gitbook.com/@0x-society/s/0x-academy/guides/what-are-nfts'
  },
  {
    type: 'question', 
    question: 'What is crypto art?',
    keywords: 'crypto art',

    content: <div className='slideparagraphs'>
      <p>
      Crypto art is a category of goods popularized by the rise of NFTs. Each piece of crypto art is a digital asset created, verified and tracked on the blockchain.
      </p>
      <p>In order to be a work of crypto art, the entire provenance of bids, sales and previous collectors must be recorded on the blockchain. </p>
    </div>,
    link: 'https://app.gitbook.com/@0x-society/s/0x-academy/guides/what-is-crypto-art'
  },
  {
    type: 'question', 
    question: 'Are NFTs protected by copyright?',
    keywords: 'NFTs and copyright',

    content: <div className='slideparagraphs'>
      <p>
      To this day, it has not already been determined by the law if NFTs are protected by copyright. However, we can interpret the law. 

      </p>
      <p>It is very unlikely that NFTs would be protected by copyright, because they simply represent data on a blockchain. In intellectual property law, that would not suffice to constitute an "original work of authorship". However, the artwork that you mint may be protected by copyright, so be careful! </p>
    </div>,
    link: 'https://app.gitbook.com/@0x-society/s/0x-academy/guides/ip-rights-and-crypto-art'
  },
  {
    type: 'question', 
    question: 'What does minting mean?',
    keywords: 'minting',

    content: <div className='slideparagraphs'>
      <p>
      Minting your work refers to the act of uploading the artwork you would like to sell and to link it to a token, representing its certificate of authenticity. 
      </p>
      <p>The first step will be to set up your wallet and get some ETH, the cryptocurrency used to pay fees to mint, list, accept bids, and more on Ethereum-based platforms.</p>
    </div>,
    link: 'https://app.gitbook.com/@0x-society/s/0x-academy/artists/untitled '
  },
  {
    type: 'question',
    question: 'How to purchase NFTs',
    keywords: 'digital art',
    content: <div className='split'>
      <div className='leftitem'>
        <p>
        To purchase NFTs, you will need to set up a cryptocurrency wallet. For more information about setting up your first crypto wallet, click 
        <a target='__blank' href='https://app.gitbook.com/@0x-society/s/0x-academy/guides/setting-up-your-eth-wallet'>
        here
        </a>
        .
        </p>
        <p>
        There are multiple marketplaces where you can purchase NFTs. Heres a few:
        </p>
      </div>
      <div className='rightitem'>
        {
          sellers.map(seller=> {
            return (
              <a className='underlinea' href={seller.link}>
                {seller.text}
                <span className='underlinespan'></span>
              </a>
            )
          })
        }
      </div>
    </div>
  },
  {
    imgs: true, 
    type: 'question',
    question: 'How to display NFTs',
    content: <div className='slidecontent'>
      <p>The misconception that you cannot display your crypto art on a wall is entirely wrong. While its still early, there are already an interesting selection of frames designed specifically to display digital art. </p>
      <div className='displayimgs'>
          <div className='displayimg'>
              <a target='__blank' href='https://www.netgear.com/home/digital-art-canvas/'>Meural</a>
              <img src='https://0xsociety.com/wp-content/uploads/2021/06/pasted-image-0.png'/>
          </div>
          <div className='displayimg'>
              <a target='__blank' href='https://www.samsung.com/ca/lifestyle-tvs/the-frame/highlights/'>Samsung The Frame</a>
              <img src='https://0xsociety.com/wp-content/uploads/2021/06/pasted-image-0-1.png'/>
          </div>
          <div className='displayimg'>
              <a target='__blank' href='https://infiniteobjects.com/'>Infinite Objects</a>
              <img src='https://0xsociety.com/wp-content/uploads/2021/06/pasted-image-0-2.png'/>
          </div>
      </div>
    </div>
  }
]