import React from 'react'
import ScrollTextEffect from '../../Reusable/ScrollTextEffect/ScrollTextEffect'
import Typeonload from '../../Reusable/Typeonload/Typeonload'

const Banner = (props) => {
  const {className, bannerref, id='' } = props
  return (
    <div id={id} ref={bannerref && bannerref} className={"banner "+className}>
      {props.children}
    </div>
  )
}
export default Banner