import React, { useContext, useEffect, useState } from 'react'
import { useLocation } from 'react-router-dom'
import { ContextApp } from '../../../../../ContextAPI'
import Article from '../../../../Reusable/Article/Article'
import Containerrow from '../../../../Reusable/Containerrow/Containerrow'
import Eventcard from './Event/Eventcard'
import './Events.css'
import AOS from 'aos'

const Events = (props) => {
  
  const [keyword, setKeyword] = useState('')
  const {clean} = useContext(ContextApp)
  const pattern = new RegExp('\\b' + clean(keyword), 'i');
  const {row, title, hidefilters, link='articles'} = props
  const [filter, setFilter] = useState('All')
  const components = {
    Eventcard, 
    Article
  }
  const length = (type) => {
    return row.filter(x=> x?.acf?.category?.some(el=> el === type )).length
  }

  const filters = [
    {
      filter: 'Digital Art',
      length: length('Digital Art')
    },
    {
      filter: 'NFT',
      length: length('NFT')
    },
    {
      filter: 'Blockchain',
      length: length('Blockchain')
    },
    {
      filter: 'Ethereum',
      length: length('Ethereum')
    },
    {
      filter: 'Minting',
      length: length('Minting')
    },
    {
      filter: 'All',
      length: row.length
    }
  ]
  const filtersrow = filters.map(filterc=> {
    return (
      <span 
      onClick={()=> setFilter(filterc.filter)}
      className={`graytext ${filterc.filter === filter?'activefilter':''}`}
      >{filterc.filter}{' '}({filterc.length})</span>
    )
  })

  const Component = components[props.component]
  const eventsrow = row.filter(x=> x?.acf?.category?.some(el=> el === filter) || filter === 'All').filter(x=> pattern.test(clean(x.acf.title)) || pattern.test(clean(x.acf?.hostedby))).map((event, i)=> {

    return  (
      <Component link={link} aos='zoom-in' event={event.acf} id={event.id} article={event.acf}/>
    )
  })
  const location = useLocation()

  useEffect(()=>{
   setKeyword('')
   AOS.refresh()
  },[location])

  return  (

    <div className="events">
  
      <Containerrow 
      className='eventsrow'
       row={eventsrow.length?eventsrow:<><h3>No {title}</h3></>} 
       title={
        <div className='eventstitle'>
          <div className="innereventtitle">
          <div></div>
          <div>{title}</div>
          <div className="search">
          <label>
            <i className='fa fa-search'></i>
            <input 
              value={keyword}
              onChange={(e)=> setKeyword(e.target.value)}
              placeholder='Filter...'/>
          </label>
          </div>
          </div>
         {
           !hidefilters && 
           <div className="filtersrow">
           {filtersrow}
         </div>
         }
        </div>
      }/>
    </div>
  )
}
export default Events