import React from 'react'
import { Link } from 'react-router-dom'
import { convertDate } from '../../../Functions'
import Appbtn from '../Appbtn/Appbtn'
import Imgloaded from '../Imgloaded/Imgloaded'
import './Article.css'

const Article = (props) => {
  const {article, link='articles', i, id, aos=''} = props
  return (
    <div className="article" data-aos={aos}>
        <Link to={`/${link}/${id}`} >
          <div className="articleimg">
            <Imgloaded img={article.img} alt=""/>
          </div>
        </Link>
        <div className="articlecont">
          <h2>{article.title}</h2>
          <small className="graytext shorttext">{article.text.slice(0, 124)}{article.text.length>124?'...':''}</small>
          <div className='readmore' >
            <span>{convertDate(article.date, true)}</span>
             <Link to={`/${link}/${id}`}>
             <span className="themeicon"></span>
              <span>Read More</span>
              </Link>
          </div>
        </div>
    </div>
  )
}
export default Article
