import React, { useContext, useEffect, useRef, useState } from 'react'
import { ContextApp } from '../../../../../../../ContextAPI'
import Article from '../../../../../../Reusable/Article/Article'
import Carouselcont from '../../../../../../Reusable/Carousel/Carousel'
import Skill from '../../../../../../Reusable/Iconbox/Skill'
import Imgloaded from '../../../../../../Reusable/Imgloaded/Imgloaded'
import Banner from '../../../../Banner'
import Articles from '../../../../Home/Articles'
import './Artistpage.css'
import Quote from './Quote'
const Artistpage = (props) => {
  const height = useRef()
  const {works, artist} = props
  const {articles} = useContext(ContextApp)
  const artistarticles = articles.filter(x=> x.acf.id === artist.id)
  const [scrolled, setScrolled] = useState(0)
  let superrare = artist.superrare
  const skillsrow = Object.keys(artist.skills).map((key)=> {
    const root = artist.skills[key]
    if(root.skill) {
      return (
        <div className="skillcol">
            <div className="skillinfo">
              <h2>{root.skill}</h2>
              <p>{root.description}</p>
              <Skill skill={root.skill}/>
            </div>
            <div className="spacercol">
              <Imgloaded img={root.img?root.img:"https://0xsociety.com/wp-content/uploads/2021/06/sLSSe2c-1-scaled.jpg"} alt=""/>
            </div>
        </div>
      )
    }
  })
  const quotesrow = artist.quotes.split('+').map(quote=> {
    return <Quote quote={quote}/>
  })


  const handleScroll = () => {
    const scrollamount = window.pageYOffset
      const amount =(scrollamount/4)
      setScrolled(amount>70?70:amount)
  
  }

  useEffect(()=>{
    window.addEventListener('scroll', handleScroll)
    return ()=> {
      window.removeEventListener('scroll', handleScroll)
    }
  }, [])

  return (
    <div className="artistpage">
      <Banner  className='herobanner' bannerref={height}>
        <div className="bannerimgcont">
         <img src={artist.img} style={{filter: `brightness(${(100-scrolled)}%)`}}/>
        </div>
        <div className="text">
          <h2>About {artist.name}</h2>
          <span className='artistdecrip'>{artist.description}</span>
          {superrare && <a target='__blank' href={superrare}><span className="themeicon"></span> <span>SuperRare Profile</span></a>}
        </div>
      </Banner>
      <div className="skillscont">
          <div className="skills">
            {skillsrow}
          </div>
      </div>
      <div className="artistquotes">
        <Carouselcont  thumbnails={false}  itemsrow={quotesrow.length?quotesrow:<><Quote skeleton/></>}/>
      </div>
      <Articles
      sublink={'artworks'}
      title={'Discover Works By '+artist.name}
      text='Lorem ipsum dolor sit amet consectetur adipisicing elit. At, dolorum?'
      articles={works}
      />
     {artistarticles.length!==0&& <Articles
      sublink={'articles'}
      title={'Discover Articles written By '+artist.name}
      text='Lorem ipsum dolor sit amet consectetur adipisicing elit. At, dolorum?'
      articles={artistarticles}
      />}
    </div>
  )
}
export default Artistpage