import React, { useEffect } from 'react'
import { accessinfo } from '../../../../../Arrays'
import Iconbox from '../../../../Reusable/Iconbox/Iconbox'
import './Grant.css'
import AOS from 'aos'
const Grant = (props) => {

  const accessinforow = accessinfo.map(access=> {
    return (
      <div  data-aos-delay={'350'} data-aos={access.aos} className="iconboxcontainer">
          <div className="innericonboxcontainer">
          <div className="iconcontbox">
            <i className={access.icon}></i>
            <span>{access.text}</span>
          </div>
          <small className='accesscont'>{access.cont}</small>
          </div>
      </div>
    )
  })

  const handleScroll = () => {
    AOS.refresh()
  }

  useEffect(()=> {
    window.addEventListener('scroll', handleScroll)

    return () => {
      window.removeEventListener('scroll', handleScroll)
    }
  },[])

  return (
    <div className="academyinfo grantinfo">
      <div className="inneracademyinfo">
        <h2>At 0x Society, our mission is to support and empower artists.</h2>
        <p>This summer, we will be hosting bi-weekly NFT competitions with our friends at ModernXarts. The winner of each competition will receive an invite to MakersPlace or Foundation AND get their minting and listing fees paid for.</p>
        <p>Later this summer, we will establish an ongoing grant program, here are some of the benefits.</p>
        <div className="accessrow">
          {accessinforow}

        </div>
      </div>
  </div>
  )
}
export default Grant