import React from 'react'
import Article from '../../../Reusable/Article/Article'
import Skeleton from '../../../Reusable/Skeleton/Skeleton'
import Titleborder from '../../../Reusable/Titleborder'

const Articles = (props) => {
  const {title, text, articles, link, sublink} = props 
  const articlesrow = articles?.map((article, i)=> {
    return <Article className='column' id={article.id} i={i} link={sublink} article={article.acf}/>
  })
 
  return (
    <div className="articles titlecont">

   <Titleborder link={link} text={text} title={title}/>
    <div className="innerarticles">
      {
        articlesrow.length? 
        articlesrow
        :
        <>
        <Skeleton className='column'/>
        <Skeleton className='column'/>
        <Skeleton className='column'/>
        </>
      }
    </div>
  </div>
  )
}
export default Articles