import React, { useState } from 'react'
import Imgloaded from '../../Imgloaded/Imgloaded'
import Modal from '../../Modal/Modal'
import Exhibitionskeleton from '../Exhibitionskeleton'


const Card = (props) => {
  const {el} = props
  const [loaded, setLoaded] = useState(false)
  const [modal, setModal] = useState(false)

  return (
    <>
     <Exhibitionskeleton style={loaded?{display: 'none'}:{}}/>
      <div 
      onClick={()=> setModal(true)}
      style={loaded?{}:{display: 'none'}} 
      className="exhibitioncont" 
      data-aos='zoom-in'>
        <img onLoad={()=> setLoaded(true)} src={el.img} alt=""/>
      </div>
      <Modal modal={modal} setModal={setModal}>
        <div className="modalcont">
          <div className="modaltext">
            <h2>{el.name}</h2>
            <span>{el.date}</span>
          </div>
         <div className="exhibitionimg">
          <Imgloaded img={el.fullimg} />
         </div>
        </div>
      </Modal>
    </>
  )
}
export default Card