import React from 'react'
import { Link } from 'react-router-dom'
import './Logo.css'

const Logo = (props) => {
  const {img, nolink} = props
  const TagName = nolink?'span':Link
  return (
    <TagName to='/' className='logo' id='logo'>
      <img src={img} alt=""/>
    </TagName>
  )
}
export default Logo