import React from 'react'
import Iconboxcont from '../../../../Reusable/Iconbox/Iconboxcont'
import Banner from '../../Banner'
import './Programs.css'
import {programs} from '../../../../../Arrays'
import Program from './Program/Program'
import Titleborder from '../../../../Reusable/Titleborder'
import Carouselcont from '../../../../Reusable/Carousel/Carousel'
import Imgloaded from '../../../../Reusable/Imgloaded/Imgloaded'
const Programs = () => {
  


  const iconsrow = programs.map(icon=> {
    return (
      <Iconboxcont icon={icon.icon} text={icon.text} title={icon.title}/>
    )
  })
  const programsrow = programs.map(program=> {
    return (
      <Program program={program} programs={programs}/>
    )
  })
  const slidesrow = programs.map(slide=> {
    return (
      <div className="programslide">
          <Titleborder 
            title='Discover our Programs'
            text={slide.title}
            />
          <div className="programslideimg">
            <Imgloaded img={slide.img} alt=""/>
          </div>
          {/* <Iconboxcont icon={slide.icon} text={slide.text} title={slide.title}/> */}
      </div>
    )
  })
  return (
    <div className="programs">
     
      <Carouselcont 
        itemsrow={slidesrow} 
       />
      <div className="programsrow">
        {programsrow}
      </div>
      <div className="exhibitions"></div>
      <div className="events"></div>
    </div>
  )
}
export default Programs