import React, { useContext, useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import { artists, artworks } from '../../../../../../Arrays'
import { ContextApp } from '../../../../../../ContextAPI'
import Imgloaded from '../../../../../Reusable/Imgloaded/Imgloaded'
import Modal from '../../../../../Reusable/Modal/Modal'
import Banner from '../../../Banner'
import Articles from '../../../Home/Articles'
import './Artwork.css'


const Artwork = (props) => {
  const {artwork, id} = props
  const {artists, artworks} = useContext(ContextApp)
  const artist = artists?.find(x=> x.acf.id === artwork.id)?.acf

  const allfeaturedimgs =  Object.keys(artwork.featuredimgs).map((key, i)=> {
    const root = artwork.featuredimgs[key]
    if(root.img) {
      
    return (
      <div className="featuredimg" data-aos={i%2?'fade-left':'fade-right'}>
      <div className="featuredcont">
        <div className="innerfcont">
          <h1>Featured Image {i+1}</h1>
          <span>{root.text}</span>
        </div>
      </div>
      <div className="featuredimgcont">
        <Imgloaded img={root.img}  />
      </div>
    </div>
    )
    }
  })



    return (
    <div className="artwork">
      <Banner className='artworkbanner'>
          <div className="bgimgcont">
            <Imgloaded img={artwork?.img} />
          </div>
          <div className="imgcont">
            <Imgloaded img={artwork?.img} />
          </div>
      </Banner>
      <div className="artworkdetails">
        <h2>{artwork?.title}</h2>
          <span>
          Artwork by: 
          <Link to={`/our-artists/${artist?.id}`}>{artist?.name} </Link>
          </span> 
          <p>{artwork?.text}</p>
      </div>
      <div className="featuredimgs">
        {allfeaturedimgs}
      </div>
    <Articles sublink={'artworks'} 
      title='Similar Artworks' text={'View similar artworks by '+artist?.name} 
      articles={artworks.filter(x=> x.acf.id === artist?.id && x.id !== id)}/>
   </div> 
  )
}
export default Artwork