import React, { useContext, useState } from 'react'
import { Link } from 'react-router-dom'
import { ContextApp } from '../../../../../ContextAPI'
import { determineLink } from '../../../../../Functions'
import Carouselcont from '../../../../Reusable/Carousel/Carousel'
import Containerrow from '../../../../Reusable/Containerrow/Containerrow'
import Iconbox from '../../../../Reusable/Iconbox/Iconbox'
import Imgloaded from '../../../../Reusable/Imgloaded/Imgloaded'
import Templatebanner from '../../../../Reusable/Templatebanner/Templatebanner'
import TemplateBannerPage from '../../../../Reusable/Templatepage/TemplateBannerPage'
import Titleborder from '../../../../Reusable/Titleborder'
import Artistcard from '../Programs/Artists/Artistcard/Artistcard'
import './Ourstory.css'
const Ourstory = () => {
  const filters = [
    'Creative Advisor',
    'Co-Founder',
    'All'
  ]
  const [filter, setFilter] = useState('All')
  const {ourteam, artists} = useContext(ContextApp)
  const filtersrow = filters.map(filterc=> {
    return (
      <span 
      onClick={()=> setFilter(filterc)} 
      className={`graytext ${filterc === filter?'activefilter':''}`}>
        {filterc}
      </span>
    )
  })
  
  const ourteamrowv2 = ourteam.filter(x=> x.acf.type === filter || filter ==='All')?.map(person=> {
    return (
    <div className='member' data-aos='zoom-in'>
        <div className="memberimg">
          <Imgloaded img={person.acf.image} />
        </div>
        <div className="membertype">
          <h3>
            <Link to={determineLink(artists, person)}>
             {person.acf.name}
            </Link>
          </h3>
          <span>{person.acf.type}</span>
        </div>
    </div>
    )
  })
  const paragraphs = [
    {
      title: '0x Society',
      text: '0x Society was founded as a non profit organization to focus on the promotion, diffusion and support of the crypto art movement.',
      icon: 'fa fa-atom'
    },
    {
      title: 'Crypto Art',
      text: 'It is the first crypto art centre in Canada, offering not only highly curated in-person and virtual exhibitions, but also thorough educational content and generous grant programs for artists.',
      icon: 'fa fa-paint-brush',
    },
    {
      title: 'Creativity and Technology',
      text: 'The congregation of creativity and technology allows us to bridge the gap between the physical and digital worlds by providing a gathering place for artists and collectors to connect.',
      icon: 'fas fa-clone'
    },
    {
      title: 'Founders',
      text: '0x Society was co-founded by Yannick Folla and Bianca Lessard, in collaboration with top Montreal artists Alycia Rainaud (Maalavidaa) and Alexy Prfontaine (Aeforia) as Creative Advisors.',
      icon: 'fa fa-users'
    }
  ]
  const steps = [
    {
      title: 'Misson',
      text: 'Enabling the digital art renaissance by supporting artists, as well as promoting and democratizing crypto art  by offering thorough educational programs and highly curated exhibitions'
    },
    {
      title: 'Vision',
      text: 'Empowering artists and collectors by bridging the gap between the physical and digital worlds'
    },
    {
      title: 'Values',
      text: 'Inclusion - Creativity - Boldness'
    }
  ]
  const stepsrow = steps.map((step, i)=> {
    return (
      <div className="step" data-aos={i%2?'fade-right':'fade-left'}>
        <div className="innerstep">
        <span className="stepnum"></span>
        <div className="stepcont">
          <span className='steptitle'>{step.title}</span>
          <span>{step.text}</span>
        </div>
        </div>
      </div>
    )
  })
  const paragraphsrow = paragraphs.map((paragraph, i)=> {
    return (
      <div   data-aos={i%2?'fade-left':'fade-right'} className="iconboxcontainer ourstoryiconbox">
          <div className="innericonboxcontainer"> 
          <div className="iconcontbox">
            <i className={paragraph.icon}></i>
            <span>{paragraph.title}</span>
          </div>
          <small className='accesscont'>{paragraph.text}</small>
          </div>
      </div>
    )
  })

  return (
    <div className="ourstory programs">
      <TemplateBannerPage 
        title='Our Story'
        text='Learn more about  0x Society'
      ></TemplateBannerPage>
      {/* <Carouselcont 
           itemsrow={ourteamrowv2} 
       /> */}
      <Containerrow
      className='storyrow' 
      title='About us'
      titleborder
      row={paragraphsrow}/>
     
     <div className="ourmission steps">
       {stepsrow}
      </div>

      <Titleborder title='Our Team'
        text={
        <>
         {filtersrow}
        </>}
      />

      <div className="ourteam">
        {ourteamrowv2}
      </div>
    </div>
  )
}
export default Ourstory