import React, {useEffect, useState} from 'react'
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from 'react-responsive-carousel';
import './Carousel.css'
const Carouselcont =  (props) => {
  const [elapsed, setElapsed] = useState(0)
  const {itemsrow, thumbails=true, autoplay=true, artists} = props
  const render = () => {
    const thumbList = artists?.map((artist, i)=> {
      return (
        <div>{i}</div>
      )
    })
    return thumbList
  }

  
  return (
    <Carousel    renderIndicator={(onClickHandler, isSelected, index, label) => {
      if (isSelected) {
          return (
              <li
                  className='selectedbtn '
                  aria-label={`Selected: ${label} ${index + 1}`}
                  title={`Selected: ${label} ${index + 1}`}
              >
                <span>{index +1}</span>
                <span className="slidepercent" style={{width: (100*elapsed/5000)}}></span>
              </li>
          );
      }
      return (
          <li
              onClick={onClickHandler}
              onKeyDown={onClickHandler}
              value={index}
              key={index}
              role="button"
              tabIndex={0}
              title={`${label} ${index + 1}`}
              aria-label={`${label} ${index + 1}`}
          >{index +1}</li>
      );
  }} autoPlay={false} interval={9000} infiniteLoop
    renderArrowNext={(onClickHandler, hasNext, label)=> 
      hasNext && (
        <span onClick={onClickHandler} className="nextbtn carouselbtn themeicon"></span>
      )
      
    }
    renderArrowPrev={(onClickHandler, hasNext, label)=> 
      hasNext && (
        <span  onClick={onClickHandler} className="nextbtn carouselbtn themeicon"></span>
      )
    }
  
  >
      {itemsrow}
    </Carousel>
  )

}
export default Carouselcont