import React from 'react'
import './Appbtn.css'
const Appbtn = (props) => {
  const {text, clickEvent, disabled=false, icon, className, i} = props
  return (
    <button 
     className={className+' appbtn reverse'}
     disabled={disabled} 
     onClick={()=> clickEvent?.() }>
        {icon&&<section></section>}
        {i && <i className={i}> </i>}
        <span>
        {text}
        </span>
    </button>
  )
}
export default Appbtn