import React from 'react'
import { Link } from 'react-router-dom'
import './Dropdown.css'

const Dropdown = (props) => {
  
  const {dropdown, link} = props

  const dropdownlinks = dropdown?.map(dropdownlink=>{
   if(dropdownlink.href) {
    return (
       <a href={dropdownlink.href} target='__blank' >
        <span>{dropdownlink.text}</span> 
        <i className='fa fa-chevron-right'></i>
       </a>
        )
   }
   else {
     return (
      <Link to={`/${dropdownlink.link}`}>
      <span>{dropdownlink.text}</span> 
      <i className='fa fa-chevron-right'></i>
    </Link>
     )
   }
    
  })

  return (
    <>
    {dropdown &&
    <div className="dropdown">
     {dropdownlinks}
    </div>
   }
    </>
  )
}
export default Dropdown