import React from 'react'
import { Link } from 'react-router-dom'
import Appbtn from '../../../../Reusable/Appbtn/Appbtn'
import Iconbox from '../../../../Reusable/Iconbox/Iconbox'
import Imgloaded from '../../../../Reusable/Imgloaded/Imgloaded'


const Container = (props) => {

  const {img, title, text, icon, href, link} = props.cont
  const btn =  <Appbtn text='Learn More' icon='fa fa-long-arrow-right'/>
  return (
    <div className="container">
        <div className="contimg">
          <Imgloaded img={img}/>
        </div>
        <div className="cont">
          <div className="iconcon">
            <i className={icon}></i>
          </div>
          <h2>{title}</h2>
          <p className="graytext">
            {text}
          </p>
         {
           href?
           <a href={href} target='__blank'>
            {btn}
           </a>
           :
           <Link to={'/'+link}>
             {btn}
           </Link>
         }
        </div>
    </div>
  )
}
export default Container