import React, { useState } from 'react'
import { HashLink } from 'react-router-hash-link'
import Logo from '../../../Reusable/Logo/Logo'
import './Column.css'

const Column =(props) => {
  const {column, logo, setMenu, notitlelink} = props
  const [activecol, setActivecol] = useState(false)


  return (
    <div className="column">

     <>
     <h3 className={activecol&&'activecoltitle'} onClick={()=> setActivecol(!activecol)}>
     
       <HashLink to={!notitlelink&&'/'+column.link} onClick={()=> setMenu?.(false)}>
          <span> {column.tab} </span>
       </HashLink>
      
       <i className={'fa fa-chevron-right'}></i>
     </h3>
     <div className={activecol?'activecol col':'col'}>
       {column.dropdown?.map(link=> {
         const El = link.link?HashLink:'a'
         return (
           <El 
           onClick={()=> {setMenu?.(false); setActivecol?.(false)}} 
           target={!link.link&&'__blank'} 
           to={`/${link.link}`} 
           href={link.href}>
             {link.icon&&<i className={link?.icon} style={{marginRight: '5px'}}></i>}{link.text}
           </El>
         )
       })}
     </div>
     </>
    </div>
  )
} 
export default Column