import React, { useState } from 'react'
import {Accordion} from '@material-ui/core';
import {AccordionSummary} from '@material-ui/core';
import {AccordionDetails} from '@material-ui/core';
import { HashLink } from 'react-router-hash-link';
const Navmenuaccordion = (props) => {

const {column, notitlelink, setMenu} = props
  const [expanded, setExpanded] = useState(false)
  return (
      <div className='acor'>
        
    <Accordion expanded={expanded}>
    <AccordionSummary
      onClick={()=> setExpanded(!expanded)}
      expandIcon={ <i className={'fa fa-chevron-right'}></i>}
      aria-controls="panel1a-content"
      id="panel1a-header"
      className='accordiontitle'
    >     
    <h3 onClick={()=> setMenu(false)}>
    <HashLink to={!notitlelink&&'/'+column.link}>
       <span> {column.tab} </span>
    </HashLink>
  </h3> 
    </AccordionSummary>
    <AccordionDetails>
          <div >
        {column.dropdown?.map(link=> {
          const El = link.link?HashLink:'a'
          return (
            <El
            onClick={()=> {setMenu(false); setExpanded(false)}}
            target={!link.link&&'__blank'} 
            to={`/${link.link}`} 
            href={link.href}>
              {link.icon&&<i className={link?.icon} style={{marginRight: '5px'}}></i>}{link.text}
            </El>
          )
        })}
      </div>
    </AccordionDetails>
  </Accordion>
      </div>
  )
}
export default Navmenuaccordion