import React from 'react'

const Menu = (props) => {

  const {navMenu, setNavMenu} = props
  
  return (
   <div className={`mobbtn ${navMenu?"open":"closed"}`} onClick={() => setNavMenu(!navMenu)}>
    <hr className="l1"/>
    <hr className="l2"/>
    <hr className="l3"/>
  </div>
  )
}
export default Menu