import React, { useContext, useEffect, useRef, useState } from 'react'
import {  questions, questionslides } from '../../../../Arrays'
import { ContextApp } from '../../../../ContextAPI'
import Academy from '../../../Reusable/0xAcademy/Academy'
import Animateddownarrow from '../../../Reusable/Animatedownarrow/Animateddownarrow'
import SlideShow from '../../../Reusable/Slideshow/Slideshow'
import Banner from '../Banner'
import Articles from './Articles'
import './Home.css'
import AOS from 'aos'
import Logo from '../../../Reusable/Logo/Logo'
import Iconbox from '../../../Reusable/Iconbox/Iconbox'
import Carouselcont from '../../../Reusable/Carousel/Carousel'
const Home = () => {

  const video = useRef()
  const {setScrolled, articles, exhibitions, artworks} = useContext(ContextApp)
  const questionsref = useRef()
  const [activequestion, setActivequestion] = useState(undefined)
  const [opacity, setOpacity] = useState(undefined)
  const curq = questions[activequestion]
  const questrow = questions.map((question, i)=> {
    return (
        <div 
        onMouseDown={()=> setOpacity(false)}
        className={`questioncont ${i===activequestion?'activequestion':''} `}  
        onClick={()=> setActivequestion(i)}>
          <div className="sect">
            <span className="themeicon questionicon rot"></span>
           <span className='questioncont'>
             <span>{question.title}</span>
           </span>
          </div>
       </div>
    )
  })
  const questionslidesrow = questionslides?.map(question=> {
    if(question.type === 'logo') {
      return (
        <div className="questionslide firstquestionslide">
            <Logo nolink img='https://0xsociety.com/wp-content/uploads/2021/06/logo-5.png'/>
            <div className="questioncont">
              {question.content}
            <div className="linkcont">
              <a target='__blank' className='underlinea' href={question.link}>Click here to start learning
              <span className="underlinespan"></span>
              </a>
            </div>
            </div>

        </div>
      )
    }
    return (
      <div className={`questionslide ${question.imgs?'questionslideimgs':''}`}>
        <h1>{question.question}</h1>
        <div className="questioncont">
          {question.content}
        {
          question.link && 
          <div className="questionlink">
          <span>
            Learn more about {question.keywords}
          </span>
          <a className='underlinea' target='__blank' href={question.link}>
          {question.link}
           <span className='underlinespan'></span>
          </a>
        </div>
        }

        </div>
      </div>
    )
  })
  useEffect(()=> {
    setScrolled(false)
  }, [])
  useEffect(()=> {
      setOpacity(true)
  }, [activequestion])
  return (
    <div className="home">

      <div  className="animationvideo">
        <div className="fullheight">
         <video id='video' playsInline={true} ref={video} loop={true}  autoPlay={true} muted={true} controls={false} src={`https://0xsociety.com/wp-content/uploads/2021/06/0x_Society_Animation_V3.mp4`}></video>
        </div>
        <Animateddownarrow href={'nft'}/>

      </div>
      <Banner className='bgwhite'id='nft'>
            <h1  data-aos='zoom-in'>Enabling the Digital Art Renaissance</h1>
            <div className="contentbg">
              <div data-aos="fade-up">
                <p>
                NFTs have kickstarted a new paradigm shift for digital artists. Artists and collectors have an opportunity to come together and build the future of the art industry. 
                </p>
                <p>
                0x Society is a non profit organization aiming to bridge the gap between the physical and digital art worlds by educating the community and promoting the crypto art movement. 
                </p>
                <p>
                Join us to experience art like never before. 
                </p>
              </div>
     
            </div>
            <Animateddownarrow href={'academy'}/>
      </Banner>
      {/* <Banner className='sl'>
          <SlideShow />
      </Banner> */}
        {/* {exhibitions.current.length?
        <Banner className='currentexhibition'>
        <img src={exhibitions.current?.find(x=> x.acf.active)?.acf.teaserimg} className='wide' alt=""/>
        <img src={exhibitions.current?.find(x=> x.acf.active)?.acf.img} className='mobile' alt=""/>
      </Banner> 
       :''} */}
      <div className='academybanners ' id='academy'>
      {/* <Banner className='academyquestions'>
          <h1 className='questiontitle'>Learn More With <Logo nolink img='https://0xsociety.com/wp-content/uploads/2021/06/logo-5.png'/></h1>
          <p>The education and onboarding platform for artists and collectors interested to learn about crypto art. 0x Academy offers thorough guides, tips, and support to help you get involved in the industry faster.</p>
          <div  className="innerquestions" ref={questionsref}>
            <div className="allquestions">
              {questrow}
            </div>
           
         <div className={`questionpopup ${ (typeof activequestion !== 'undefined')?'activequestionmodal':''} `}>
           <div className={`widthanimator ${opacity?'opacityanimate':''}`}></div>
          <div className="innerquestionpopup">
           <Iconbox icon='fa fa-atom questionicon'/>
            <h3>{curq?.title}</h3>
            <p>{curq?.text}</p>
            <a href={curq?.link} target='__blank'>
             <span className="themeicon questionicon rot"> Learn More</span>
            </a>
          </div>
          </div>

          </div>
  
      </Banner>   */}
      <Banner className='academyquestions academyslides'>
         <Carouselcont itemsrow={questionslidesrow} />
       </Banner>
      </div>
   
  
        <Articles 
        text='View All'
        sublink={'artworks'}
        link={'artworks'}
        articles={artworks.slice(0, 3)}
        title={'Featured Artworks'}/>

    </div>
  )
}
export default Home