import React, { useState } from 'react'
import { convertDate } from '../../../../../../Functions'
import Imgloaded from '../../../../../Reusable/Imgloaded/Imgloaded'
import Modal from '../../../../../Reusable/Modal/Modal'

const Eventcard = (props) => {
  const {aos=''} = props
  const {img, title, date, hostedby, description, cost ,location} = props.event
  const [modal, setModal] = useState(false)
  return (
   <>
    <div className="eventcard" data-aos={aos}>
      <div onClick={()=> setModal(true)} className="eventimgcont">
        <Imgloaded img={img} />
      </div>
      <div className="eventcont">
        <div className="eventdate">{convertDate(new Date(Date.parse(date)))}</div> 
        <h3>{title}</h3>
        <small>Hosted By: {hostedby}</small>
      </div>
    </div>
    <Modal modal={modal} setModal={setModal}>
        <div className="eventcontmodal">
            <div className="eventcont">
              <h3>{title}</h3>
              <div className="eventdate">{convertDate(new Date(Date.parse(date)))}</div> 
              <span>Location: {location}</span>
              <span>Cost: {cost}</span>
              <div className="descriptionevent">
                {description}
              </div>
              <small>Hosted By: {hostedby}</small>
           </div>
        </div>
    </Modal>
   </>
  )
}
export default Eventcard