import React from 'react'
import './Iconbox.css'

const Iconbox = (props) => {
  const {text, icon, aos} = props
  return (
    <div className="iconbox" data-aos={aos}>
      <div className="iconcon">
        <i className={icon}> </i>
      </div>
    {text}
  </div>
  )
}
export default Iconbox