import React, { useContext, useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import { ContextApp } from '../../../../../ContextAPI'
import Appbtn from '../../../../Reusable/Appbtn/Appbtn'
import Imgloaded from '../../../../Reusable/Imgloaded/Imgloaded'
import Banner from '../../Banner'
import './LostPage.css'

const LostPage = () => {
  const {setScrolled} = useContext(ContextApp)
  const [textshadow, setTextshadow] = useState({
    x: 450,
    y: 300
  })
  function deterMineShadow(e) {
    setTextshadow({
      x: e.clientX,
      y: e.clientY
    })
  }
  useEffect(()=> {
    setScrolled(false)
    return () => {
      setScrolled(true)
    }
  },[])

  return (
    <div className="lostpage youarelost" onMouseMove={e=>deterMineShadow(e)}>
        <Banner>
            <div className='lostcont'>
              <p style={{textShadow: `rgb(0 0 0 / 33%) -${textshadow.x/15}px ${textshadow.y/30}px 10px`}}>{`4{}4`}</p>
              <Link to='/'>
                <Appbtn text='Return Home'/>
              </Link>
            </div>
          <Imgloaded img={'https://0xsociety.com/wp-content/uploads/2021/06/2xztxQ4-1-scaled.jpg'} />
        </Banner>
    </div>
  )
}
export default LostPage