import React from 'react'
import { Link } from 'react-router-dom'

const Titleborder = (props) => {
  const {title, text, link, classNameTitle} = props
  
  
  return (
    <div className='titlecont'>
    <h1>
      <span className={classNameTitle}>{title}</span>
      {
        link?
        <Link to={link}>
          <small>{text}</small>
          <i className="arrow"></i>
        </Link>
        :
        <small>{text}</small>
      }
    </h1>
    </div>
  )
}
export default Titleborder